@import url(https://fonts.googleapis.com/css?family=Titillium+Web&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  font-family: 'Titillium Web';
  box-sizing: border-box;
}

.img-film {
    width: 250px;
    border-radius: 20px;
}

.card-img {
    margin: 30px;
}

h1 {
    display: flex;
    justify-content: center;
}

.footer {
    display: flex;
    justify-content: center;
}

.footer a {
    text-decoration: none;
}

.loading {
    display: flex;
    justify-content: center;
    margin-top: 100px;
}

.container-img {
    display: flex;
    margin-top: 55px;
    height: auto;
    flex-wrap: wrap;
    justify-content: center;
}
.banner {
    background-image: url(/swapi-react-app/static/media/banner4.e45c1aa9.jpg);
    background-size: cover;
    background-position: center;
    min-height: 60vh;
    position: relative;
}
.banner-film {
    background-image: url(/swapi-react-app/static/media/4.5455003e.jpg);
    width: 100%;
    background-size: cover;
    background-position: center;
    min-height: 60vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.container-film {
    margin: 0 auto;
    width: 80%;
}

.film-info h1 {
    justify-content: left;
}

.starships {
    height: 100px;
}

.container-card {
    display: flex;
    height: auto;
    flex-wrap: wrap;
    justify-content: center;
}

.image {
    width: 100%;
    border-radius: 20px;
}

.card-image {
    max-width: 120px;
    margin: 25px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.film-info-2 {
    display: flex;
}

.film-info-2 p {
    margin-right: 20px;
}

.image-film {
    width: 100%;
    border-radius: 20px;
}

.film-details {
    background: rgba(0, 0, 0, 0.7);
    max-width: 1280px;
    min-height: 300px;
    display: flex;
    border-radius: 20px;
    margin: 25px;
}

.film-image {
    min-width: 300px;
}

.film-info {
    color: white;
    padding: 30px;
}

/* Responsive */

@media only screen and (min-width: 501px) and (max-width: 845px) {
    .film-details {
        display: block;
        width: 70%;
        margin: 30px auto;
    }
    .film-image {
        min-width: 200px;
    }
    .image-film {
        width: 100%;
    }
}

/* Mobile */

@media only screen and (min-width: 0px) and (max-width: 500px) {
    .film-details {
        display: block;
        width: 90%;
        margin: 20px auto;
    }
    .film-image {
        min-width: 50px;
    }
    .film-info2 {
        display: block;
    }
    .container-card {
        flex-wrap: nowrap;
        overflow-x: auto;
        justify-content: normal;
    }

    .card-image {
        flex: 0 0 auto;
        margin: 5px;
    }
}
